.login-user-dd {
  @media (min-width: 1023px) {
    order: 3;
    padding-left: 15px;
  }
  .dropdown-menu {
    border: none;
    box-shadow: $box-shadow;
    margin-top: 15px;
    a {
      padding: 0.75rem 1rem;
      color: $dark;
      &:hover {
        background: $dropdown-a-bg;
        color: $dark;
      }
    }
  }
  .dropdown-toggle {
    display: flex;
    align-items: center;
    @media (max-width: 991px) {
      &:after {
        display: none;
      }
    }
    img {
      @media (max-width: 991px) {
        display: none;
      }
    }
    i {
      display: none;
      @media (max-width: 991px) {
        display: block;
      }
    }
  }
}



.header-search-dropdown {
  position: absolute;
  top: 5px;
  right: 0;

  @media (min-width: 991px) {
    top: 50%;
    @include transform(translateY(-50%))
  }

  .dropdown-menu {
    border: none;
  }
}

.navbar {
  padding: 0;
  .navbar-brand {
    color: $black;
    display: block;
    margin-right: 30px;
  }

  .logo-white {
    display: none;
  }

  .navbar-toggler {
    font-size: 24px;
    border: none;
    padding: 0;
    color: $primary;
    margin-left: auto;
    line-height: 0;
    margin-right: 15px;
    &:focus {
      outline: none;
    }
  }

  .navbar-nav {
    @media (max-width: 991px) {
      .dropdown-menu {
        box-shadow: none !important;
        .dropdown-item {
          padding: 1rem 0;
          border-bottom: none;
        }
      } 
    }
    @media (max-width: 767px) {
      overflow-y: auto;
      max-height: 250px;
    }
    .nav-link {
      line-height: 26px;
      color: $dark;
      @include transition(all 0.2s ease-in-out);
      padding: 0.5rem 1.25rem;
      position: relative;
      font-size: 15px;
      &.dropdown-toggle {
        padding-right: 1.5rem;
        &:before {
          content: "\ea17";
          font-family: $primaryIcons;
          @include position(absolute, 0, 0.5rem, 0, null);
          margin: auto 0;
          height: 16px;
          width: 16px;
          line-height: 1;
        }
      }
      @media (max-width: 991px) {
        padding: 0.6rem 0;
      }
      &:after {
        height: 25%;
        bottom: 10px;
        opacity: 0.3;
        left: 15px;
        right: 15px;
        content: "";
        position: absolute;
        @include transform(scaleX(0));
        transform-origin: 0% 50%;
        @include transition(all 0.5s cubic-bezier(0.2, 1, 0.3, 1));
        @media (max-width: 991px) {
          display: none;
        }
      }

      &.active,
      &:hover,
      &:focus {
        color: $primary;

        &:after {
          @include transform(scaleX(1));
          background: $primary;
        }
      }

      &:active {
        color: $primary;
      }
    }

    @media (max-width: 991px) {
      background: $white;
      margin-top: 15px;
    }
  }
}

.header {
  padding: 20px 0px;
  @include transition(all 0.5s ease-in-out);
  @include position(fixed, 0, 0, null, 0, 999);
  @include border(bottom, 1px, solid, $border-color);
  background: $white;
  @media (max-width: 991px) {
    padding: 15px 0;
    .navbar-expand-lg>.container, .navbar-expand-lg>.container-fluid {
      padding: 0 15px;
    }
  }

  .navbar-header {
    @media (max-width:991px) {
      display: flex;
      align-items: center;
      .logo-dark , .logo-white {
        height: 32px;
      }
    }
  }
  &.fixed-nav {
    margin-top: 0px;
    padding: 15px 0px;
    background-color: $white;
    box-shadow: 0 9px 20px 0px rgba($black, 0.05);
    @media (max-width: 991px) {
      padding: 10px 0;
    }
    .logo-dark {
      display: block;
    }

    .logo-white {
      display: none;
    }

    .navbar-nav {
      .nav-link {
        color: rgba($black, 0.8);

        &.active,
        &:hover,
        &:focus {
          color: $primary;

          &:after {
            @include transform(scaleX(1));
            background: $primary;
          }
        }
      }

      .show > .nav-link {
        color: rgba($black, 0.8);

        &.active,
        &:hover,
        &:focus {
          color: $primary;

          &:after {
            @include transform(scaleX(1));
            background: $primary;
          }
        }
      }
    }
  }
}

.navbar-light {
  .navbar-toggler {
    color: $white;
  }

  .logo-dark {
    display: none;
  }

  .logo-white {
    display: block;
  }

  .navbar-nav {
    .nav-link {
      color: rgba($white, 0.8);

      @media (max-width: 991px) {
        color: rgba($dark, 0.8);
      }

      &.active,
      &:hover,
      &:focus {
        color: $white;

        @media (max-width: 991px) {
          color: $primary;
        }

        &:after {
          @include transform(scaleX(1));
          background: $white;
        }
      }
    }

    .show > .nav-link {
      color: $white;

      @media (max-width: 991px) {
        color: rgba($dark, 0.8);
      }

      &:after {
        @include transform(scaleX(1));
        background: $white;

        @media (max-width: 991px) {
          background: $dark;
        }
      }
    }
    @media (max-width: 991px) {
      margin-top: 15px;
    }
  }
}

.navbar-collapse {
  .login-nav {
    margin-left: 15px;
    align-items: center;
    border-left: 1px solid $border-color;
    @media (max-width:991px) {
     margin-top: 0;
     margin-left: 0;
     border-left: none;
     align-items: flex-start;
    }
    li {
      @media (max-width:991px) {
        width: 100%;
      }
      .nav-link {
        padding: 0.5rem 1.5rem;
        @media (max-width:991px) {
         padding: 0.5rem 0;
        }
        line-height: 26px;
      }
    }
  }
}


















