.download-list {
  .downloads {
    display: flex;
    justify-content: space-between;
    padding: 30px;
    align-items: center;
    margin-bottom: 30px;
    h5 {
      margin-bottom: 0;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  .demo-item {
    .title {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 15px;
    }
  }
}