
.avatar {
  width: 40px;
  height: 40px;
  display: inline-block;
  line-height: 40px;
  text-align: center;
  background: $white;
  color: $primary;
  border-radius: 100%;
  i {
    line-height: inherit;
  }
}

.avatar-lg {
  width: 50px;
  height: 50px;
  line-height: 50px;
}

@each $key, $val in $icon-sizes {
  .icon-#{$key} {
    font-size: #{$val}px;
  }
}
