.container-fluid {
  width: 90%;
  @media (max-width: 991px) {
    width: 100%;
  }
}

html {
  font-size: 16px;
}

body {
  font-family: $font-primary;
  color: rgba($dark, 0.8);
  font-size: 1rem;
  line-height: 1.75;
  overflow-x: hidden;
}

a {
  // font-family: $font-secondary;
  // font-weight: 500;
  color: lighten($primary, 10);
  &:hover {
    text-decoration: none;
    color: $primary;
  }
  &.default-color {
    color: $default-color;
    &:hover {
      color: $dark;
    }
  }
}


h1, .h1,
h2, .h2,
h3, .h3,
h4, .h4,
h5, .h5,
h6, .h6,
strong {
  font-family: $font-secondary;
  color: $dark;
  // font-weight: 700;
}

h1, .h1,
h2, .h2 {
  line-height: 1.25;
}

h3, .h3,
h4, .h4 {
  line-height: 1.5;
}

h5, .h5,
h6, .h6 {
  line-height: 1.75;
}

h1, .h1 {
  font-size: px-rem-converter(40);
  @media (max-width: 767px) {
    font-size: px-rem-converter(36);
  }
}

h2, .h2 {
  font-size: px-rem-converter(32);
  @media (max-width: 767px) {
    font-size: px-rem-converter(28);
  }
}

h3, .h3 {
  font-size: px-rem-converter(28);
  @media (max-width: 767px) {
    font-size: px-rem-converter(24);
  }
}

h4, .h4 {
  font-size: px-rem-converter(24);
  @media (max-width: 767px) {
    font-size: px-rem-converter(20);
  }
}

h5, .h5 {
  font-size: px-rem-converter(18);
}

h6, .h6 {
  font-size: px-rem-converter(16);
}