.contact-info {
  .address-info {
    margin-bottom: 30px;
    display: flex;
    .icon {
      background: $primary;
      width: 50px;
      height: 50px;
      display: inline-block;
      display: none;
      text-align: center;
      line-height: 50px;
      border-radius: 100%;
      color: $white;
      margin-right: 30px;
      font-size: 24px;
    }
  }
  strong {
    display: block;
    margin-bottom: 8px;
  }
  p {
    margin-bottom: 0;
  }
}