.theme-title {
  padding: 45px 0;
  // background: $light;
  @media (max-width: 767px) {
    padding: 20px 0 45px;
  }
}

.theme-actions {
  .checklist-tabs {
    .checklist {
      display: none;
      li {
        padding: 5px 0;
      }
      &:first-child {
        display: block;
      }
    }
  }

  .licence-link {
    display: block;
    font-size: 13px;
    width: 100%;
    text-align: right;
    a {
      text-decoration: underline;
      font-weight: 600;
    }
  }
}

.package-type {
  ul {
    @include border(null, 1px, solid, $border-color);
  }
  li {
    display: flex;
    align-items: center;
    padding: 15px;
    flex-wrap: wrap;
    @include border(bottom, 1px, solid, $border-color);
    @include border(left, 3px, solid, transparent);
    @include transition(all 0.3s);
    &:last-child {
      border-bottom: none;
    }
    &.selected,
    &:hover {
      background: lighten($light, 2);
      border-left-color: darken($primary, 2);
    }
    &.selected {
      .radio {
        &:after {
          @include transform(scale(1));
        }
      }
    }
  }
  .licence-type {
    display: flex;
    align-items: center;
    .radio {
      margin-right: 10px;
      width: 16px;
      height: 16px;
      border: 1px solid;
      border-radius: 100%;
      position: relative;
      &:after {
        content: "";
        @include position(absolute, 0, 0, 0, 0);
        width: 8px;
        height: 8px;
        background: $primary;
        margin: auto;
        border-radius: 100%;
        @include transform(scale(0));
        @include transition(all 0.3s);
      }
    }
    .subtext {
      font-size: 13px;
      // font-weight: 500;
    }
  }
  .licence-value {
    .subtext {
      font-size: 13px;
      // font-weight: 500;
    }
  }
  .licence-detail-btn {
    @include position(absolute, -35px, 0, null, null);
    padding: 5px;
    @include border(null, 1px, solid, $border-color);
    border-bottom: none;
    display: none;
    align-items: center;
    i {
      opacity: 0.7;
    }
    &:hover {
      color: $dark;
      i {
        opacity: 1;
      }
    }
  }
}

.slider-area {
  @media (max-width: 991px) {
    margin-bottom: 30px;
  }
  .live-example {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 15px;
    flex-wrap: wrap;
  }
}

.tech-details {
  display: flex;
  .tooltip-wrap {
    display: flex;
    padding: 5px;
    position: relative;
    &:hover {
      .tooltip {
        opacity: 0.9;
      }
    }
  }
}

.package-tabs {
  .nav-tabs {
    @media (max-width: 579px) {
      justify-content: center;
    }
  }
  .nav-item {
    margin-bottom: 0;
    .nav-link {
      position: relative;
      border: none;
      padding: 0.75rem 1.5rem;
      color: $dark;
      @media (max-width: 579px) {
        span {
          display: none;
        }
      }
      &:after {
        content: "";
        height: 2px;
        width: 100%;
        @include position(absolute, null, 0, -1px, 0);
        background: $primary;
        @include transform(scale(0));
        transform-origin: center;
        @include transition(all 0.3s);
      }
    }
  }
  .nav-tabs {
    .nav-item.show {
      .nav-link {
        color: $dark;
        .bx {
          color: $primary;
        }
        &:after {
          @include transform(scale(1));
        }
      }
    }
    .nav-link.active {
      color: $dark;
      .bx {
        color: $primary;
      }
      &:after {
        @include transform(scale(1));
      }
    }
  }
  .tab-content {
    padding-top: 50px;
  }
}

.pricing {
  .pricing-header {
    padding: 30px;
    @include border(bottom, 1px, solid, $border-color);
  }
  .pricing-body {
    padding: 30px;
    li {
      padding: 0.5rem 0;
    }
  }
  .pricing-footer {
    padding: 0 30px 30px;
  }
}

.initial-logs {
  padding: 30px;
  background: $light;
  display: flex;
  flex-wrap: wrap;
  .logs {
    flex: 1;
    display: flex;
    align-items: center;
    span {
      font-size: 24px;
    }
    @media (max-width: 767px) {
      flex: auto;
      width: 50%;
      margin-bottom: 1rem;
    }
    @media (max-width: 579px) {
      width: 100%;
    }
  }
}

.theme-facts {
  .facts {
    display: flex;
    flex-wrap: wrap;
    @include border(bottom, 1px, solid, $border-color);
    &:first-child {
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    &:last-child {
      border-bottom: none;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .card {
      border-left: none;
      border-top: none;
      border-bottom: none;
      flex: 1;
      background: transparent;
      border-color: $border-color;
      border-radius: 0;
      &:last-child {
        border-right: none;
      }
      @media (max-width: 767px) {
        flex-basis: 50%;
        border-right-color: transparent;
        border-bottom: 1px solid $border-color;
        &:nth-child(odd) {
          border-right-color: $border-color;
        }
      }

      @media (max-width: 479px) {
        flex-basis: 100%;
        &:nth-child(odd) {
          border-right-color: transparent;
        }
      }
    }
  }
}

.theme-demos {
  .head-text {
    margin-bottom: 45px;
  }
  .demo-theme-item {
    padding-bottom: 15px;
    margin-bottom: 50px;
    @include border(bottom, 1px, solid, $border-color);
    img {
      box-shadow: $box-shadow;
      @media (max-width: 767px) {
        width: 100%;
      }
    }
    h5 {
      padding-top: 30px;
      margin-bottom: 0.5rem;
    }
    .desc {
      a {
        color: $default-color;
        &:hover {
          color: $primary;
          opacity: 1;
        }
      }
    }
  }
}

.payment-options {
  .options {
    display: flex;
  }
  .pay-btn {
    border: none;
    background: none;
    border: 1px solid transparent;
    display: flex;
    align-items: center;
    padding: 8px 15px;
    border-radius: 50px;
    margin-right: 15px;
    img {
      width: 12px;
      margin-right: 4px;
    }
    i {
      margin-right: 3px;
    }
    &:last-child {
      margin-right: 0;
    }
    &.selected {
      background: darken($light, 3);
      border-color: darken($light, 8);
    }
    &:focus, &:hover {
      outline: none;
    }
    &:hover {
      opacity: 0.8;
    }
  }
  #paypal-button-container {
    height: 50px;
  }

  .pay-options {
    display: flex;
    align-items: center;
  }
}
