.link {
  color: $default-color;
  font-weight: 600;
  padding-bottom: 10px;
  border-bottom: 2px solid $border-color;
  float: right;
  position: relative;
  &:hover {
    color: $default-color;
  }
  i {
    vertical-align: middle;
  }
}

.overlay {
  @include position(absolute, 0, 0, 0, 0);
  background: $black;
}

.original-price {
  margin-left: 10px;
  text-decoration: line-through;
  font-size: 20px;
  opacity: 0.4;
}

.section {
  padding: 75px 0;
}

.box-shadow {
  box-shadow: $box-shadow;
}

.bg-gred {
  @include gradient($gred-primary);
}

.number-list {
  list-style: decimal;
}

.social-icons {
  display: flex;
  align-items: center;
  a {
    width: 35px;
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    margin-right: 10px;
    @include border(null, 1px, solid, transparent);
    &:hover {
      background: $primary;
      color: $white;
    }
    &:last-child {
      margin-right: 0;
    }
  }
  &.badge-icons {
    a {
      margin-right: 10px;
      background: rgba($primary, 0.1);
      color: $primary;
      border-color: $primary;
      &:hover {
        background: $primary;
        color: $white;
      }
    }
  }
  &.social-white {
    a {
      background: $white;
      color: $primary;
    }
  }
}

.symbol {
  width: 60px;
  height: 60px;
  border-radius: 4px;
  background: $primary;
  line-height: 60px;
  text-align: center;
  display: inline-block;
}

.bg-pattern {
  background-image: url(../images/bg-pattern.png);
  background-size: cover;
  background-position: center;
}

.dotted-border {
  @include border(bottom, 1px, dotted, $default-color);
}

.success-msgs {
  display: none;
}

.error-msgs,
.success-msgs {
  margin-bottom: 30px;
  padding: 0.5rem 2rem 0.5rem 1rem;
  display: none;
  p {
    margin-bottom: 0.5rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  .close {
    @include position(absolute, 11px, 5px, null, null);
    padding: 0;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.gutter-size-25 {
  margin: 0 -25px;
  [class^="col-"] {
    padding: 0 25px;
    @media (max-width: 767px) {
      padding: 0 15px;
    }
  }
  @media (max-width: 767px) {
    margin: 0 -15px;
  }
}

.keyword-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  li {
    color: $white;
    font-size: 13px;
    margin: 0 5px;
    a {
      color: $white;
      text-decoration: underline;
    }
    &:last-child {
      margin-right: 0;
    }
  }
}

.checklist {
  li {
    display: flex;
    align-items: center;
    padding: 0.5rem 0;
    color: $dark;
    a {
      color: $primary;
      text-decoration: underline;
    }
    &:last-child {
      margin-bottom: 0;
    }
  }
  &.checklist-align-top {
    li {
      align-items: flex-start;
    }
  }
}

.disable-events {
  opacity: 0.8;
  pointer-events: none;
  position: relative;
  span:first-child {
    visibility: hidden;
    opacity: 0;
  }
  .btn-loader {
    display: flex;
  }
}

.back-top {
  @include position(fixed, null, 30px, 50px, null, 3);
  opacity: 0;
  visibility: hidden;
  z-index: -3;
  @include transform(translateY(-10px));
  @include transition(all 0.4s);
  a {
    background: $primary;
    color: $white;
    width: 36px;
    height: 36px;
    border-radius: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @media (max-width: 767px) {
    right: 15px;
  }  
  &.show {
    opacity: 1;
    visibility: visible;
    z-index: 3;
    @include transform(translateY(0));
  }
}

.heading {
  position: relative;
  margin-bottom: 40px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-wrap: wrap;
  h2 {
    margin-bottom: 0;
  }

  h6 {
    font-weight: 400;
    color: $default-color;
    margin-bottom: 0;
  }

  .view-all {
    float: right;
    font-size: px-rem-converter(15);
    span {
      display: inline-block;
      @include transition(all 0.3s);
    }
    &:hover {
      span {
        padding-left: 5px;
      }
    }
  }

  &:before {
    @include position(absolute, null, null, 0, 0);
    background: $gred-primary;
    height: 3px;
    width: 80px;
    content: "";
    display: none;
    margin: auto;
  }

  &.white-text {
    h2 {
      color: $white;
    }

    &:before {
      background: $white;
    }
  }

  &.text-center {
    &:before {
      left: 0;
      right: 0;
      margin: auto;
    }
  }
}


.view-all {
  color: $primary;
  position: relative;
  padding: 0 10px 10px 10px;
  &:after {
    height: 8px;
    bottom: 12px;
    opacity: .3;
    left: 0;
    right: 0;
    content: "";
    position: absolute;
    background: $primary;
  }
}
