
.dropdown-menu {
  font-size: px-rem-converter(14);
  line-height: px-rem-converter(24);
  overflow: hidden;
}

.dropdown-item {
  padding: 1rem 1.75rem;
  @include border(bottom, 1px, solid, $border-color);
  display: flex;
  align-items: center;
  &:last-child {
    border-bottom: none;
  }
  i {
    font-size: 24px;
    margin-right: 15px;
  }
  &.active,
  &:active {
    background: none;
    color: $primary;
  }
}
