
@for $i from 1 to 11 {
  .mt-#{$i*5},
  .my-#{$i*5} {
    margin-top: $i * 5px;
  }
  .mb-#{$i*5},
  .my-#{$i*5} {
    margin-bottom: $i * 5px;
  }
  .mr-#{$i*5},
  .mx-#{$i*5} {
    margin-right: $i * 5px;
  }
  .ml-#{$i*5},
  .mx-#{$i*5} {
    margin-left: $i * 5px;
  }
  .m-#{$i*5} {
    margin-bottom: $i * 5px;
  }
  .pt-#{$i*5},
  .py-#{$i*5} {
    padding-top: $i * 5px;
  }
  .pb-#{$i*5},
  .py-#{$i*5} {
    padding-bottom: $i * 5px;
  }
  .p-#{$i*5} {
    padding: $i * 5px;
  }
}

.mb-75 {
  margin-bottom: 75px;
}

.mt-100 {
  margin-top: 100px;
}

.pb-100 {
  padding-bottom: 100px;
}