.rounded-pill {
  border-radius: 50rem !important;
}

.rounded-50 {
  border-radius: 50px;
}

@for $i from 1 to 4 {
  .border-radius-#{$i}x {
    border-radius: $i * 5px;
  }
  .border-blr-radius-#{$i}x {
    border-bottom-left-radius: $i * 5px;
    border-bottom-right-radius: $i * 5px;
  }
  .border-tlr-radius-#{$i}x {
    border-top-left-radius: $i * 5px;
    border-top-right-radius: $i * 5px;
  }
}
