

.accordion {
  .card {
      box-shadow: none;
      margin-bottom: 30px;
      border-radius: 8px !important;
      border: 1px solid transparent;
      transition: all 0.3s;
      box-shadow: 0 0 4px 2px rgba(0,0,0,.03);
      &:not(:last-of-type) {
          border-bottom: 1px solid transparent;
      }
      &:not(:first-of-type):not(:last-of-type) {
        border-bottom: 1px solid transparent;
    }
    &.active {
        border-color: $primary !important;
        .accordion-toggle {
            &:before {
                opacity: 0;
            }
            &:after {
                opacity: 1;
            }
        }
    }
  }
  .card-header {
      background: none;
      border: none;
      padding: 0;
      .accordion-toggle {
          position: relative;
          cursor: pointer;
          display: flex;
          color: $dark;
          padding: 30px 55px 30px 30px;
          // font-weight: 500;
          &:before {
              content: "\eb89";
              position: absolute;
              top: 0;
              bottom: 0;
              width: 36px;
              height: 36px;
              margin: auto;
              right: 15px;
              text-align: center;
              line-height: 36px;
              border-radius: 100%;
              color: $primary;
              font-family: $primary-icons;
              background: rgba($primary, 0.1);
              opacity: 1;
              transition: all 0.3s;
          }
          &:after {
              content: "\eb56";
              position: absolute;
              top: 0;
              bottom: 0;
              width: 36px;
              height: 36px;
              margin: auto;
              right: 15px;
              text-align: center;
              line-height: 36px;
              border-radius: 100%;
              color: $white;
              font-family: $primary-icons;
              background: $primary;
              opacity: 0;
              transition: all 0.3s;
          }
      }
  }
  .card-body {
      padding: 0 30px 30px;
  }
}
