.home-banner {
  padding: 190px 0 100px;
  background-image: linear-gradient(87deg, #3023ae, #ff6ca7);
  background-size: cover;
  width: 100%;
  .search-box {
    input {
      padding-left: 20px;
      height: 48px;
      padding-right: 50px;
      &:focus {
        border-color: transparent;
      }
    }
    .icon {
      background: $white;
      color: $dark;
      top: 1px;
      font-size: 20px;
      font-weight: 600;
      height: 46px;
      &:focus {
        outline: none;
        box-shadow: none;
      }
    }
  }
  /**
    * ----------------------------------------------
    * Banner content  css
    * ----------------------------------------------
    */
  .banner-content {
    position: relative;
    z-index: 2;
    h1 {
      line-height: 2.75rem;
      color: $white;
      margin-bottom: 30px;
    }
    p {
      @media (max-width: 767px) {
        font-size: 16px;
      }
    }
  }
}

.banner-title {
  padding-top: 50px;
  @media (max-width: 767px) {
    padding: 20px 0;
  }
  .divider {
    bottom: -1px;

    @media (max-width: 767px) {
      bottom: -5px;
    }

    img {
      width: 100%;
    }
  }

  .subtitle {
    // color: $gray-200;
    
    color: $default-color;
    font-weight: 400;
    // font-family: $font-secondary;
    margin-bottom: 0;
    @media (max-width: 991px) {
      margin-bottom: 30px;
    }
  }

  // @media (max-width: 767px) {
  //   padding: 70px 0 95px;
  // }
}
