.demo-item {
  position: relative;
  overflow: hidden;
  margin-bottom: 50px;
  display: block;
  text-align: center;
  // box-shadow: $box-shadow;
  .action-btn {
    text-transform: capitalize;
    text-align: center;
    color: $white;
    @include position(absolute, 15px, null, null, 15px, 2);
    padding-bottom: 30px;
    font-size: 1.5rem;
    @include transition(all 0.4s ease-in-out);
    opacity: 0;
    display: flex;
    @media (max-width: 991px) {
      width: 100%;
    }
  }

  .screenshot {
    position: relative;
    display: block;
    overflow: hidden;
    box-shadow: $box-shadow;
    border-radius: $border-radius;
    i {
      font-size: 32px;
      color: $angular-icon-color;
      @include position(absolute, null, null, 15px, 0);
      padding: 8px;
      border-radius: 0 5px 5px 0;
      background: $white;
      box-shadow: 0 4px 5px rgba(0, 0, 0, 0.05), 0 0 15px rgba(0, 0, 0, 0.1);
    }
    img {
      @include transition(all 0.3s);
      max-width: 100%;
      @media (max-width: 767px) {
        width: 100%;
      }
    }
  }

  .theme-desc {
    padding: 30px 0 15px 0;
    align-items: center;
    text-align: left;
    @include border(bottom, 1px, solid, $border-color);
    .title {
      overflow: hidden;
      margin-bottom: 25px;
      display: block;
      h3 {
        line-height: 1.25;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-bottom: 10px;
        // font-weight: 400;
        a {
          color: $dark;
          &:hover {
            color: $dark;
          }
        }
      }
      p {
        margin-bottom: 0;
        font-size: 14px;
      }
    }
    .price {
      display: flex;
      justify-content: space-between;
      align-items: center;
      h4 {
        margin-bottom: 0;
      }
      a {
        color: $primary;
        &:hover {
          color: darken($primary, 10);
        }
      }
      .btn {
        color: $white;
        &:hover {
          color: $white;
        }
      }
    }
  }

  &:hover {
    .action-btn {
      opacity: 1;
    }
  }
}
