footer {
  background: $footer-bg;
  padding-top: 100px;
  color: $muted;
  .f-logo {
    width: 150px;
    display: block;
  }

  a {
    color: $muted;
    @include transition(all 0.3s);
    &:hover {
      color: $white;
    }
  }
  .f-links {
    li {
      padding: 5px 0;
    }
  }
  h2 {
    color: $white;
    margin-bottom: 1rem;
    line-height: 1.5;
  }
  h5 {
    @media (max-width: 991px) {
      margin-top: 15px;
    }
    @media (max-width: 767px) {
      margin: 15px 0 !important;
    }
  }
  .copyright {
    margin-top: 30px;
    padding: 30px 0;
    width: 100%;
    @include border(top, 1px, solid, rgba($border-color, 0.1));
    text-align: center;
  }
}
