
.validation-error {
  font-size: px-rem-converter(12);
  color: $err-color;
}

.form-control {
  height: calc(2.9rem + 1px);
  border: 1px solid rgba($muted, 0.3);
  padding: 10px 15px;
  &:disabled {
    background: darken($light, 5) !important;
  }
  &:focus {
    box-shadow: none;
    border-color: $primary;
  }
}

.form-control-sm {
  height: calc(2.5rem + 2px);
  padding: 8px 15px;
  font-size: 1rem;
}

.form-group {
  margin-bottom: 30px;
  &.invalid-field {
    .form-control {
      border-color: $err-color;
    }
  }
}


.input-group {
  .form-control {
    &:focus {
      z-index: 0;
    }
  }
}

.app-label {
  position: relative;

  label {
    color: $default-color;
    padding: 0 5px;
    @include position(absolute, 11px, null, null, 10px);
    cursor: text;
    @include transition(all 0.25s ease-out 0.05s);
  }

  .form-control {
    background: none;

    &:focus {
      box-shadow: none;
      border-color: $primary;

      & + label {
        top: -11px;
        background: $white;
        @include transform(scale(0.9));
        color: $primary;
      }
    }
  }

  &.up {
    .form-control {
      border-color: $primary;
    }

    label {
      top: -11px;
      background: $white;
      @include transform(scale(0.9));
      color: $primary;
    }
  }

  &.invalid-field {
    .form-control {
      border-color: $err-color;

      &:focus {
        border-color: $err-color;
      }
    }

    label {
      top: -11px;
      background: $white;
      @include transform(scale(0.9));
      color: $err-color;
    }
  }
}

.search-btn {
  width: 40px;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $primary;
  color: $white;
  border-radius: 100%;

  &:hover,
  &:focus {
    color: $white;
  }

  @media (max-width: 991px) {
    width: 35px;
    height: 35px;
  }
}

.header-search-dropdown {
}

.search-box {
  input {
    padding-right: 40px;
    min-width: 225px;
    height: 40px;
    padding-left: 10px;
  }

  .icon {
    @include position(absolute, 0, 0, 0, null, 1);
    width: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $dark;
    background: none;
    border-top-right-radius: 0.25rem;
    border-bottom-right-radius: 0.25rem;
    box-shadow: none;
    border: none;
    cursor: pointer;
  }
}

.hide-product-btn {
  background: $black;
  width: 720px;
  @include position(absolute, 60px, 0, null, -33px, 222222);
  margin: 0 auto;
  height: 0;
  display: none;
  i {
    width: 32px;
    height: 32px;
    background: transparent;
    cursor: pointer;
    position: relative;
    display: block;
    margin-left: 19px;
    margin-top: 19px;
  }
  &.show {
    display: block;
  }
}