.highlight-section {
  margin-bottom: 120px;
  margin-top: 25px;
  .media {
    align-items: center;
    @media (max-width: 991px) {
      align-items: flex-start;
    }
    @media (max-width: 767px) {
      flex-wrap: wrap;
    }
  }
  .dotted-wrap {
    position: relative;
    margin-right: 50px;
    @media (max-width: 767px) {
      width: 100%;
      margin-bottom: 50px;
      margin-right: 0;
    }
    &:after {
      border: 1px dashed lighten($primary, 20%);
      position: absolute;
      content: '';
      top: 0;
      left: 0;
      right: 0;
      bottom: 0px;
      width: 100%;
      height: 100%;
      margin: 15px;
      border-radius: 8px;
    }
  }
  .image {
    padding: 30px;
    border-radius: 8px;
    background: rgba(lighten($light, 2), 0.9);
    position: relative;
    z-index: 1;
    @media (max-width: 991px) {
      img {
        width: 250px;
      }
    }
    @media (max-width: 767px) {
      img {
        width: 100%;
      }
    }
  }
}

.feature {
  padding: 30px;
  box-shadow: $feature-shadow;
  border-radius: 4px;
  text-align: center;
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  .icon {
    font-size: 48px;
    color: $primary;
    text-shadow: 5px 5px 5px rgba($black, 0.1);
    @include transition(all 0.3s)
  }
  .h6 {
    font-weight: 400;
    margin-bottom: 0;
  }
  &:hover {
    .icon {
      text-shadow: none;
    }
  }
}