.testimonials-slider {
  .item {
    padding: 30px;
    margin: 15px;
    text-align: center;
    position: relative;
    background: $white;
    border-radius: 8px;
  }
  .author-image {
    img {
      @include position(absolute, -42px, null, null, 50%);
      @include transform(translateX(-50%));
      width: 84px;
      border-radius: 50%;
      margin: 0px auto 40px auto;
    }
  }

  .background-image {
    img {
      @include position(absolute, 50%, null, null, 50%);
      @include transform(translate(-50%, -50%));
      max-width: 100px;
      opacity: 0.75;
    }
  }

  h5 {
    margin-bottom: 5px;
    letter-spacing: 0.5px;
  }

  span {
    font-size: px-rem-converter(16px);
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: $primary;
    font-size: px-rem-converter(13);
  }
}