.home-btn {
  @include position(absolute, 30px, 30px, null, null);
}

.modal {
  .close {
    @include position(absolute, 0, 3px, null, null, 1);
    width: 32px;
    height: 32px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: $white;
    border-radius: 100%;
    @media (max-width: 579px) {
      right: 8px;
    }
    cursor: pointer;
    opacity: 1;
    &:focus {
      outline: none;
    }
    &:not(:disabled):not(.disabled):focus, &:not(:disabled):not(.disabled):hover {
      opacity: 0.85;
    }
    &:hover {
      opacity: 0.85;
    }
  }
}

.account-pages {
  min-height: 100vh;
  display: flex;
  align-items: center;
}


.auth-modal {
  .auth-form {
    .close {
      background: none;
    }
  }

  .modal-dialog {
    max-width: 400px;
  }
  .modal-content {
    padding-bottom: 30px;
  }
  .modal-header {
    padding: 30px 30px 15px 30px;
    border: none;
    align-items: center;
  }
  .modal-body {
    padding: 15px 30px 0 30px;
  }

  .modal-footer {
    padding: 15px 30px;
    margin-top: 15px;
    border-top: none;
    background: $light;
  }
}

.password-group {
  .form-control {
    padding-right: 40px;
  }
  .icon-view {
    @include position(absolute, 0, 0, null, null, 1);
    width: 40px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $dark;
    cursor: pointer;
    &:hover {
      color: $primary;
    }
  }
  .bx-hide.icon-view {
    display: none;
  }
  &.active {
    .bx-show.icon-view {
      display: none;
    }
    .bx-hide.icon-view {
      display: flex;
    }
  }
}

.thankyou-modal {
  .bx-check-circle {
    font-size: 60px;
    font-weight: 300;
  }
  .cancel-icon {
    display: flex;
    align-items: center;
    justify-content: center;
    i {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 50px;
      height: 50px;
      background: lighten(red, 10);
      color: $white;
      font-style: normal;
      font-size: 36px;
      border-radius: 100%;
    }
  }
  .modal-header {
    padding: 0;
    border-bottom: 0;
  }
}
